import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CepTitulo, CLASS_NAME } from '../../../../models/dtos/ger/cepTitulo';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'nome',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const CepTituloSearchView: FC<SearchViewProps<CepTitulo>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'nome',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Nome',
    },
  ];

  return (
    <WindowComponent title="Manutenção de Tipo de Logradouro">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, '', CLASS_NAME, { hideActionColumn: true })}
        addButtonProps={{ hideAddButton: true }}
      />
    </WindowComponent>
  );
};

export default CepTituloSearchView;
